<template >
  <div class="mt72">
    <div class="myBox">
      <div class="boxLeft">
        <div class="xueke">
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <div class="subject">
            <subject />
          </div>
        </div>
      </div>
      <div class="boxRight">
        <div class="myImg">
          <img src="../../../static/images/章错题列表.png" alt />
        </div>
        <div class="table">
          <table>
            <thead>
              <tr style="background-color:#f8f8f8;">
                <td style="color:#fb8b22;font-size:16px;padding:5px">序号</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">资源名称</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">类型</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">功能</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">能力范围</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">来源</td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in list"
                :key="index"
                style="margin-bottom:5px"
                class="trHover"
              >
                <td style="padding:10px;font-size:16px">{{ index + 1 }}</td>
                <td style="padding:10px;font-size:16px">
                  <a href="javascript:void(0);">内能</a>
                </td>
                <td style="padding:10px;font-size:16px">课件</td>
                <td style="padding:10px;font-size:16px">预习</td>
                <td style="padding:10px;font-size:16px">基础学习</td>
                <td style="padding:10px;font-size:16px">陈老师</td>
              </tr>
            </tbody>
          </table>
          <el-pagination
            layout="prev, pager, next"
            :total="100"
            class="page"
            style="color: #2ab8b3;"
          ></el-pagination>
        </div>
        <div class="testBox">
          <div class="testLeft">
            <img src="../../../static/images/test.png" alt />
          </div>
          <div class="testRight">
            <el-button style="background-color:#2cb9ae;border:0;color:#fff;margin:30px auto">章错题自改</el-button>
            <el-button
              style="background-color:#ff8b00;border:0;color:#fff;margin:0px auto"
            >&nbsp;&nbsp;&nbsp;教师批改</el-button>
            <el-button
              style="background-color:#2cb9ae;border:0;color:#fff;margin:20px auto;display:block"
            >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;打印&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import subject from "../../components/grade/subject";
export default {
  components: {
    subject
  },
  data() {
    return {
      list: [1, 1, 1, 1, 1, 1, 1, 1],
      value: "",
      options: [
        { value: "选项1", label: "语文" },
        { value: "选项2", label: "数学" },
        { value: "选项3", label: "英语" }
      ]
    };
  }
};
</script>
<style lang="less">
.mt72 {
  background-color: #fff;
  .myBox {
    background-color: #fff;
    width: 100%;
    display: flex;
    justify-content: space-between;
    .boxLeft {
      width: 208px;
      border-right: 10px solid #eee;
      .subject {
        border-top: 3px solid #eee;
      }
    }
    .boxRight {
      flex: 1;
      .myImg {
        height: 70px;
        img {
          margin-top: 20px;
        }
      }
      .table {
        width: 100%;
        //  height: 50%;
        margin: 0 auto;
        text-align: center;
        // border: 1px solid #2ab8b3;
        //  margin-bottom: 15px;
        border-bottom: 10px solid #f8f8f8;
        table {
          width: 100%;
          height: 100%;
          // text-align: center;
          padding: 18px;
          .trHover:hover {
            background-color: #faf1ed;
          }
        }
        .page {
          position: relative;
          margin: 5px;
          right: -318px;
          bottom: 0;
        }
      }
      .testBox {
        display: flex;
        justify-content: space-between;
        .testLeft {
          flex: 1;
        }
        .testRight {
          border-left: 1px solid #eee;
          width: 194px;
          text-align: center;
        }
      }
    }
  }
}
</style>


